/* CSS Document */
html, body {
  margin: 0;
  padding: 0;
  font: normal 16px/1.5 Arial, "微軟正黑體";
  color: #333333;
  background-color: #f4f4f4; }

a {
  text-decoration: none;
  border: none;
  outline: none;
  color: 333333;
  outline: none;
  /* for Firefox */
  hlbr: expression(this.onFocus=this.blur());
  /* for IE */
  cursor: pointer; }

a:hover {
  color: red;
  text-decoration: none; }

li {
  list-style: none; }

div, p, span, ul, ol, li, h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6, input, table, td, th, img {
  margin: 0;
  padding: 0; }

img {
  border: none;
  display: block; }

.clr {
  clear: both;
  display: block; }

.tR {
  text-align: right; }

.tL {
  text-align: left; }

.tC {
  text-align: center; }

.fL {
  float: left; }

.fR {
  float: right; }

.phone {
  display: none; }

.m_hide {
  display: block; }

.m_block {
  display: none; }

h2, .h2 {
  letter-spacing: 0.5px;
  font-size: 30px; }

h3, .h3 {
  letter-spacing: 0.5px;
  font-size: 24px; }

/*頁面設定*/
#pagesize {
  position: relative;
  margin: 0 auto; }

/*header*/
header {
  position: relative;
  height: 80px;
  background: #fff url(../images/header_bg.png) right no-repeat;
  background-size: auto 100%;
  border-bottom: 45px solid transparent;
  -moz-border-image: -moz-linear-gradient(left, #093d7c 0%, #0081cc 100%);
  -webkit-border-image: -webkit-linear-gradient(left, #093d7c 0%, #0081cc 100%);
  border-image: linear-gradient(to right, #093d7c 0%, #0081cc 100%);
  border-image-slice: 1; }

header h1, header .h1 {
  position: absolute;
  left: 40px;
  top: 20px; }

header h1 a, header .h1 a {
  width: 340px;
  height: 40px;
  display: block;
  background: url(../images/logo.png) no-repeat; }

header h2, header .h2 {
  position: absolute;
  right: 40px;
  top: 20px;
  font-size: 25px;
  color: #0358aa; }

header h2 a, header .h2 a {
  color: #0358aa; }

header nav {
  position: absolute;
  top: 80px;
  left: 40px;
  width: calc(100% - 80px);
  height: 45px;
  display: block;
  text-align: right; }

header nav ul li {
  line-height: 45px;
  display: inline-block;
  background: url(../images/nav_bg.png) right no-repeat; }

header nav ul li a {
  position: relative;
  height: 45px;
  padding: 0 30px;
  display: block;
  color: #fff; }

header nav ul li:last-child {
  background: none; }

header nav ul li:last-child a {
  padding: 0 0 0 30px; }

